import { ZodType } from 'zod';
import type { IBaseService } from './base.service.interface.js';

export default class BaseService implements IBaseService {

  private static defaultInit: RequestInit = {
    headers: { 'Content-Type': 'application/json' }
  };

  protected get baseUrl(): string | URL | undefined {
    return undefined;
  }

  protected async post<TResponse>(schema: ZodType, url: string | URL | undefined, data?: { [key: string]: any }, init?: RequestInit): Promise<TResponse> {
    if (!init)
      init = BaseService.defaultInit;

    init.method = 'POST';
    init.body = JSON.stringify(data);
    const response = await this.fetch(url, init);
    if (!response.ok) {
      return Promise.reject();
    }

    return this.tryParse<TResponse>(response, schema);
  }

  protected async get<TResponse>(schema: ZodType, url: string | URL | undefined, searchParams?: string | Record<string, string> | string[][] | URLSearchParams | undefined, init?: RequestInit): Promise<TResponse> {
    if (!init)
      init = BaseService.defaultInit;

    searchParams = new URLSearchParams(searchParams);
    const params = searchParams.size == 0 
      ? '' 
      : `?${searchParams.toString()}`;
    url = `${url}${params}}`;

    init.method = 'GET';
    const response = await this.fetch(url, init);
    if (!response.ok) {
      return Promise.reject();
    }

    return this.tryParse<TResponse>(response, schema);
  }

  protected fetch(url: string | URL | undefined, init?: RequestInit): Promise<Response> {
    url = `${this.baseUrl}${url ? url : ''}`;
    init = { ...BaseService.defaultInit, ...init };
    return fetch(url, init);
  }

  protected async tryParse<TResponse>(response: Response, schema: ZodType): Promise<TResponse> {
    const json = await response.json();
    console.log(json);
    return await schema.parseAsync(json) as TResponse;
  }

}