import type { AccountData, CreateAccountData, GetAccountsData, UpdateAccountData } from '@strife/common';
import { CreateAccountSchema } from '@strife/common';
import { injectable } from 'inversify';
import di from '../app/modules/di';
import type { IAccountService } from './account.service.interface';
import { AccountServiceServiceId } from './account.service.interface';
import BaseService from './base.service';

@injectable()
class AccountService extends BaseService implements IAccountService {

  protected override get baseUrl(): string | URL | undefined {
    return `http://localhost:3000/accounts`;
  }

  public createAccount(data: CreateAccountData): Promise<AccountData> {
    return this.post<AccountData>(CreateAccountSchema, undefined, data);
  }

  public deleteAccount(): Promise<void> {
    throw new Error('Method not implemented.');
  }

  public getAccount(id: string): Promise<AccountData> {
    throw new Error('Method not implemented.');
  }

  public getAccounts(data: GetAccountsData): Promise<AccountData[]> {
    throw new Error('Method not implemented.');
  }

  public updateAccount(data: UpdateAccountData): Promise<AccountData> {
    throw new Error('Method not implemented.');
  }

}

di.bind(AccountServiceServiceId).to(AccountService).inSingletonScope();